/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(189, 32%, 55%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(189, 32%, 87%);
  --clr-primary-8: rgb(165, 213, 248);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(209, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background : url("background-large.png");
  background-repeat:no-repeat;
  background-size: cover;
  background-position: center;
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
@media screen and (max-width: 900px) {
  body{
    background : url("background-medium.png");
    background-position: center;
    font-size: 0.5rem;
    }
  }
  @media screen and (max-width: 600px) {
  body{
    background : url("background-small.png");
    background-position: center;
    font-size: 0.25rem;
    }
  }
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
h4 {
  font-size: 1.5rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-3);
}
@media screen and (max-width: 900px) {
  h1 {
    font-size: 2rem;
  }
  h2 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1.25rem;
  }
  h4 {
    font-size: .5rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
@media screen and (max-width: 600px) {
  h1 {
    font-size: 1.75rem;
  }
  h2 {
    font-size: 1.25rem;
  }
}
/*  global classes */

/* section */
.section {
  width: 90vw;
  margin: 0 auto;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}

/*
=============== 
Quiz
===============
*/

main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quiz {
  /* width: 90vw; */  
  /* max-width: var(--max-width); */
  width : 700px;
  max-width: 40rem;
  margin: 4rem auto;
  background: var(--clr-white);
  border-radius: var(--radius);
  padding: 3rem;
}
  @media screen and (max-width: 900px) {
  .quiz{
    max-width: 30rem;
    }
  }
  @media screen and (max-width: 600px) {
  .quiz{
    max-width: 25rem;
    }
  }



.container h2 {
  margin-bottom: 2rem;
  text-align: center;
  line-height: 1.5;
  text-transform: none;
}
.logo-container{
  display: flex;
  flex-direction: column;
}
.logo{
  width : 80%;
  margin:0 auto;
}
.result_icons{
  width : 30%;
}
 @media screen and (max-width: 900px) {
  .result_icons{
    width : 50%;
    }
  }
.btn-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
  @media screen and (max-width: 600px) {
  .btn-container{
    grid-template-columns: repeat(1, 1fr);
    }
  }
.answer-btn {
  display: block;
  width: 80%;
  margin: 0.5rem auto;
  background: var(--clr-primary-7); 
  border-radius: var(--radius);
  border-color: transparent;
  color: var(--clr-black);
  letter-spacing: var(--spacing);
  font-size: 1rem;
  cursor: pointer;
  padding: 0.5rem 0.5rem;
  transition: var(--transition);
}

@media screen and (max-width: 900px) {
  .answer-btn {
    max-width: 80%;
  }
}
@media screen and (max-width: 600px) {
  .answer-btn {
    max-width: 80%;
  }
}
.answer-btn:hover {
  background: var(--clr-primary-5);
  color: var(--clr-white);
  font-weight: 700;
}

.questions-count {
  font-size: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: capitalize;
  letter-spacing: var(--spacing);
  color: #26808f;
}

.next-question,
.close-btn,
.submit-btn {
  border-radius: var(--radius);
  border-color: transparent;
  padding: 0.25rem 0.5rem;
  display: block;
  width: 35%;
  margin: 0 auto;
  margin-top: 2rem;
  text-transform: capitalize;
  font-weight: 700;
  letter-spacing: var(--spacing);
  font-size: 1rem;
  background: #FFA800;
  color: var(--clr-black);
  transition: var(--transition);
  cursor: pointer;
}
.next-question{width : 50%;}
.next-question:hover,
.close-btn:hover,
.submit-btn:hover {
  background: #805900;
  color: #FFA800;
}
@media screen and (max-width: 900px) {
.next-question,
.close-btn,
.submit-btn {
    width: 60%;
    margin-top: 1.5rem;
  }
}
@media screen and (max-width: 600px) {
.next-question,
.close-btn,
.submit-btn {
  font-size: 1rem;
  width: 70%;
  margin-top: 1rem;
  }
}
/*
=============== 
Form
===============
*/

.setup-form h2 {
  margin-bottom: 2rem;
}
.form-control {
  margin-bottom: 2rem;
}
.form-control label {
  display: block;
  text-transform: capitalize;
  font-weight: 500;
  color: var(--clr-grey-3);
  margin-bottom: 0.5rem;
}
.form-input {
  border: none;
  background: var(--clr-grey-10);
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  width: 100%;
  border-radius: var(--radius);
}
.error {
  color: var(--clr-red-dark);
  text-transform: capitalize;
}
.submit-btn {
  width: 100%;
  margin-top: 3rem;
}
/*
=============== 
Loading
===============
*/
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  margin-top: 10rem;
  border-radius: 50%;
  border: 3px solid #ccc;
  border-top-color: var(--clr-primary-5);
  animation: spinner 0.6s linear infinite;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: var(--transition);
  z-index: -1;
}
.isOpen {
  opacity: 1;
  z-index: 999;
}

.modal-content {
  background: var(--clr-white);
  width: 40vw;
  /* max-width: var(--fixed-width); */
  padding: 2rem;
  border-radius: var(--radius);
  text-align: center;
  position: relative;
}

.modal-content p {
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: none;
}
@media screen and (max-width: 900px) {
.modal-content{
  width : 50vw;
}
.modal-content p {
  font-size: 1.25rem;
  }
}
@media screen and (max-width: 600px) {
.modal-content{
  width : 60vw;
  padding: 2rem 1rem;
}
.modal-content p {
  font-size: 1rem;
  }
}
.close-btn {
  margin-right: auto;
}
/* -------------
result modal 
------------- */
.results{
  display: flex;
  justify-content: space-around;
  margin : 1rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 900px) {
.results {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
}
.result_item{
  display: flex;
  flex-direction: column;
  background : #26808F;
  border-radius: 5px;
  max-width:100%;
  padding : 0 1rem;
  margin : 0 1rem;
}
.result_item p {
  font-size: 1rem;
  font-weight: 500;
  padding-top: 10px;
  color :var(--clr-grey-9);
}
.result_item h3{
  color : #fff;
}
.result_buttons{
  display: flex;
}

@media screen and (max-width: 500px) {
  .result_item p {
  font-size: .5rem;
  }
}

@media screen and (max-width: 600px) {
.results {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
  .result_item p {
  font-size: .75rem;
  }
  .result_item h3 {
  font-size: .6rem;
  }
  .result_buttons {
  flex-direction: column;
  }
}

@media screen and (max-width: 800px) {
.result_item p {
  font-size: 1rem;
  }
  .result_item h3 {
  font-size: .8rem;
  }
}

@media screen and (max-width: 900px) {
.result_item {
  flex-direction: row;
  margin : .5rem 1rem;
  padding : .5rem 1rem;
  padding-bottom: 0;
  align-items: center;
  }
  .result_item p {
  font-size: 1rem;
  }
  .result_item h3 {
  font-size: 1rem;
  margin-left: 10px;
  }
  .result_buttons {
  flex-direction: column;
  }
}

@media screen and (max-width: 1260px) {
.result_item h3 {
  font-size: 1.5rem;
  }
}

.score{
  display: flex;
  flex-direction: column;
}
.score p{
  font-size: 1.5rem;
  color : var(--clr-grey-2);
  font-weight: 500;
}
@media screen and (max-width: 900px) {
.score p {
  font-size: 1.25rem;
  }
}

.score h1{
  font-size: 3rem;
  color:#FFA800
}